import React, {Component} from 'react';

class About extends Component {
    render() {
        let description, age, phone, address, email, service, image;
        if (this.props.data) {
            image = this.props.data.image;
            description = this.props.data.description;
            age = this.props.data.age;
            phone = this.props.data.phone;
            address = this.props.data.address;
            email = this.props.data.email;
            service = this.props.data.service.map(function (item, index) {
                return  <div key={index} className="col-lg-4 col-sm-6">
                            <div className="single-core-service mt-40">
                                <i className={"icon " + item.icon}></i>
                                <h5>{item.name}</h5>
                                <p>{item.description}</p>
                            </div>
                        </div>
            });
        }
        return (
            <section id="about">
                <div className="main-wrapper-bg">
                    <div className="theme-container">
                        <h2 className="theme-title">Giới <span>thiệu .</span></h2>
                        <div className="about-block">
                            <div className="text">
                                <div className="row">
                                    <div className="col-12">
                                        <p className="cd-headline clip is-full-width">
                                            {description}
                                        </p>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-5 col-lg-4 col-md-5 order-md-last"><img
                                        src={image} alt=""
                                        className="my-img"/></div>
                                    <div className="col-xl-7 col-lg-8 col-md-7 order-md-first">
                                        <ul className="clearfix profile-list">
                                            <li><span>Tuổi:</span> {age} Years</li>
                                            <li><span>Thành phố:</span> {address}</li>
                                            <li><span>Email:</span> {email}</li>
                                            <li><span>Số điện thoại:</span>{phone}</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/*<div className="service-block pt-130">*/}
                        {/*    <h2 className="theme-title-two">Servi<span>ces.</span></h2>*/}
                        {/*    <div className="row">*/}
                        {/*        {service}*/}
                        {/*    </div>*/}
                        {/*</div>*/}
                    </div>
                </div>
            </section>
        );
    }
}

export default About;
