import React, {Component} from 'react';
import ReactHtmlParser from "react-html-parser";

class Project extends Component{
    render () {
        let project;
        if (this.props.data) {
            project =  this.props.data.map(function (item, index) {
                return  <div key={index} className="isotop-item">
                    <figure className="single-item">
                        <img src={item.image} alt={item.name} />
                        <figcaption>
                            <h3><a href={item.url}>{item.name}</a></h3>
                            <a data-fancybox="gallery"
                               href={item.image} className="fancybox" data-caption={item.name}>
                                <i className="fa fa-picture-o" aria-hidden="true"></i></a>
                        </figcaption>
                    </figure>
                </div>
            })
        }
        return (
            <section id="portfolio" className="portfolio-section">
                <div className="main-wrapper-bg">
                    <div className="theme-container">
                        <h2 className="theme-title">Dự <span>án.</span></h2>
                        <div className="project-gallery-wrapper mt-40">
                            <div id="isotop-gallery-wrapper" className="row mt-40">
                                <div className="grid-sizer"></div>
                                {project}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Project;
