import React,{Component} from 'react';

class Contact extends Component{
    render() {
        let address, phone, email;
        if (this.props.data) {
            address = this.props.data.address;
            phone = this.props.data.phone;
            email = this.props.data.email;
        }
        return (
            <section id="contact" className="contact-section">
                <div className="main-wrapper-bg">
                    <div className="theme-container">
                            <h2 className="theme-title">Liên <span>hệ.</span></h2>
                        <div className="contact-address pt-35">
                            <div className="row">
                                <div className="col-md-4">
                                    <div className="single-block">
                                        <div className="icon"><i className="fa fa-location-arrow" aria-hidden="true"></i>
                                        </div>
                                        <h5>Địa chỉ</h5>
                                        <p>{address}</p>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="single-block">
                                        <div className="icon"><i className="fa fa-phone" aria-hidden="true"></i></div>
                                        <h5>Số điện thoại</h5>
                                        <p>{phone}</p>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="single-block">
                                        <div className="icon"><i className="fa fa-envelope-o" aria-hidden="true"></i></div>
                                        <h5>Email</h5>
                                        <p>{email}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }

}

export default Contact;
