import React, {Component} from 'react';
import './Home.css';
class Home extends Component{
	render() {
		let name, work, social;
		if (this.props.data) {
			name = this.props.data.name;
			work = this.props.data.work;
			social = this.props.data.social.map(function (item, index) {
				return <li key={index}><a href={item.url}><i className={item.icon} aria-hidden="true"></i></a></li>
			})
		}
		return (
			<section id="home" className="bg-hero">
				<div className="partical-bg">
					<div id="particles"></div>
				</div>
				<div className="main-wrapper">
					<div className="img-box"><img src="/assets/img/hoang_cv.jpg" alt="" className="user-img"/></div>
					<h1 className="author-name">{name}</h1>
					<h4>
						<p>{work}</p>
					</h4>
					<ul className="social-icon">
						{social}
					</ul>
				</div>
			</section>
		);
	}

}

export default Home;
